  .header__enlace {
    margin-top: 5rem;
  }
}
.header__logo {
  font-size: clamp(3rem, -0.8rem + 10vw, 8rem);
  background: linear-gradient(to right, #2563EB, #293241);
  background-clip: text;
  color: transparent;
}
.header span {
  font-size: 1.2rem;
}

.footer__texto {
  font-size: 1.4rem;
  text-align: center;
}
@media (min-width: 768px) {
  .footer {
    position: fixed;
    bottom: 0;
    width: 100%;
  }
}
.footer__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
  font-size: 1.4rem;
}

