html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 3rem;
}

.contenedor-m {
  width: 100%;
  max-width: 100rem;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 80rem;
  margin: 0 auto;
}

.contenedor-grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 95%;
}
@media (min-width: 768px) {
  .contenedor-grid {
    flex-direction: row;
    width: 100%;
  }
  .contenedor-grid img {
    width: 50rem;
  }
}

.text-center {
  text-align: center;
  margin: 1rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.swal2-popup {
  position: fixed;
  width: 40rem;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .swal2-popup {
    max-width: 300px;
  }
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #1E293B;
  line-height: 1.8;
  margin: 0;
}

.formulario {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  border-radius: 0.5rem;
  background-color: #293241;
  width: auto;
  margin: 2rem 1rem 1rem 1rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -20px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}
.formulario__fieldset {
  border: none;
  margin: 1rem 1rem 2rem 1rem;
  padding: 1rem;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  border-bottom: 3px solid #000;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario__legend {
    font-size: 3rem;
  }
}
.formulario__head {
  display: flex;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  font-weight: 700;
  color: #ff5500;
}
.formulario__label--span {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .formulario__label {
    font-size: 3rem;
  }
}
.formulario__label--texto {
  color: #FFFFFF;
}
.formulario__texto--alerta {
  text-align: center;
  font-size: 2.3rem;
  font-weight: 700;
  background-color: #ffdcdc;
  border-left: 0.5rem solid #a90000;
  color: #a90000;
}
.formulario__texto {
  color: #FFFFFF;
}
.formulario__input {
  padding: 1rem;
  border: 1px solid #b1c7dd;
  background-color: #e8ecf1;
  border-radius: 0.5rem;
  font-weight: 700;
  font-size: 3rem;
  margin-bottom: 1.5rem;
  width: 100%;
}
.formulario__input--folio-cliente {
  width: 12rem;
}
.formulario__input--factura-cliente {
  width: 10rem;
}
.formulario__input--folio-material {
  width: 12rem;
}
.formulario__input--cantidad-material {
  width: 8rem;
}
.formulario__input--num-almacen {
  width: 5rem;
}
.formulario__input--cantidadfacturas {
  width: 15rem;
  font-size: 2rem;
}
.formulario__input--clientesatendidos {
  width: 15rem;
  font-size: 2rem;
}
.formulario__input--horas-llegada {
  width: 20rem;
  font-size: 2rem;
}
.formulario__input--file {
  width: 65%;
}
.formulario__input--file:hover {
  cursor: pointer;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.formulario__imagen {
  max-width: 20rem;
}
.formulario__textarea {
  width: 100%;
  height: 10rem;
  resize: none;
  border: none;
  border-radius: 0.5rem;
  font-weight: 700;
  font-size: 1.8rem;
}
.formulario__container {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
.formulario__container input, .formulario__container [type=date] {
  padding: 1rem;
  border: 1px solid #ff5500;
  border-radius: 0.5rem;
  background-color: #bac2ce;
  font-size: 1.3rem;
  outline: none;
  width: 40rem;
}
.formulario__container input:hover, .formulario__container [type=date]:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .formulario__container {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.formulario .box1, .formulario .box2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.formulario__datos {
  color: #FFFFFF;
}
.formulario__select, .devolucion__select {
  border: none;
  border-radius: 0.5rem;
  padding: 1rem;
  font-size: 1.4rem;
  font-weight: 700;
  outline: none;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .formulario__select, .devolucion__select {
    font-size: 3rem;
  }
}
.formulario__select__option {
  background-color: #F8FAFC;
}
.formulario__submit {
  background-color: #13181f;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
  align-self: "";
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  width: auto;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
.formulario__submit:hover {
  background-color: #090c0f;
  color: #e6e6e6;
  cursor: pointer;
}
@media (min-width: 768px) {
  .formulario__submit {
    background-color: #13181f;
    padding: 1.5rem 4rem;
    color: #FFFFFF;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: 0.3s;
    text-align: center;
    display: block;
    width: 100%;
    border-radius: 0.5rem;
