@media (min-width: 768px) and (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
@media (min-width: 768px) {
  .formulario__submit:hover {
    background-color: #090c0f;
    color: #e6e6e6;
    cursor: pointer;
  }
