  color: #FFFFFF;
  font-size: 2.5rem;
}
.modal .vista-modal {
  max-width: 55rem;
  background-color: #333e51;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition: all 500ms ease-in-out;
  opacity: 0;
  border-radius: 1rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
.modal .vista-modal .cerrar-modal {
  background-color: #ff5500;
  color: #000;
  border: none;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.modal .vista-modal.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .vista-modal.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}

